.Persik {
  display: block;
  width: 30%;
  max-width: 240px;
  margin: 20px auto;
}

.vivod {
	margin: 5px auto;
}

body {
	background-color: black;
}

.black-vlad-lox {
	color: black;
}	

.btn-ogan-vlad-lox {
	color: black;
	font-family: Arial, 'Times New Roman', Times;
	padding-left: 5px;
	padding-bottom: 2px;
}

.link {
	text-decoration: none;
}

.stavka-vlad-lox {
    padding: 9px 40px;
    border-radius: 35px;
    background: #fff;
    font-weight: 600;
    font-size: 25px;
    box-shadow: 0 0 6px rgba(0,0,0,.4);
}

.game {
    padding: 9px 40px;
    border-radius: 35px;
    background: #fff;
    font-weight: 600;
    font-size: 25px;
    box-shadow: 0 0 6px rgba(0,0,0,.4);
}

.game__coin {
    display: inline-block;
    vertical-align: -6px;
}

.game--btn-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
    flex-direction: row;
    -webkit-align-items: center;
    align-items: center;
    margin-bottom: 17px;
}

.game--final-bets {
    z-index: 5;
    position: fixed;
    bottom: 2vh;
    right: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
    flex-direction: column;
    -webkit-justify-content: flex-end;
    justify-content: flex-end;
    -webkit-align-items: center;
    align-items: center;
    padding-top: 5px;
    background: linear-gradient(#ffffff) no-repeat;
    background-size: cover;
}

.game--vlad-gurov-lox {
    z-index: 5;
    position: fixed;
    bottom: 56vh;
    right: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
    flex-direction: column;
    -webkit-justify-content: flex-end;
    justify-content: flex-end;
    -webkit-align-items: center;
    align-items: center;
    padding-top: 5px;
    background: linear-gradient(#ffffff) no-repeat;
    background-size: cover;
}

.game--vlad-gurov--lox {
    z-index: 5;
    position: fixed;
    bottom: 45vh;
    right: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
    flex-direction: column;
    -webkit-justify-content: flex-end;
    justify-content: flex-end;
    -webkit-align-items: center;
    align-items: center;
    padding-top: 5px;
    background: linear-gradient(#ffffff) no-repeat;
    background-size: cover;
}
